import * as React from "react"
import CustomImage from "./custom_image"

const containerStyle = {
  display: "flex",
  flexGrow: 1,
  alignItems: "center",
  justifyContent: "space-between",
  gap: 50
}
const titleStyle = {
  fontFamily: "Roboto",
  fontWeight: 400
}
const imageStyle = {
  width: "50vw",
  border: 0
}

export default function TextImage(props) {
  return (
    <div style={{
      ...containerStyle,
      ...{margin: props.narrowWindow ? "0px 0px 0px 0px" : "-90px -105px -105px 0px"}}}>
      <h1 style={{
        ...titleStyle,
        ...{fontSize: props.narrowWindow ? 20: 25}
      }}>{props.title}</h1>
      { 
        props.narrowWindow ? <div/> : 
          <CustomImage 
            imageName={props.imageName}
            alt={props.alt} 
            objectPosition={"right"}
            style={imageStyle}
          />
      }
    </div>
  )
}
